<template>
  <v-container id="dashboard-view" fluid tag="section">
    <v-row justify="center">
      <v-col cols="12">
        <material-card color="primary" icon="mdi-wallet">
          <template #title>
            <h2>ALL TRANSACTIONS</h2>
          </template>
          <v-container class="py-0">
            <v-data-table
              :headers="headers"
              :items="transactions"
              class="elevation-1"
              loading-text="Loading... Please wait"
              v-if="search == ''"
            >
              <!--verified or not-->
              <template v-slot:item.status="{ item }">
                <v-chip v-if="item.status == 1" color="green" label>
                  Verified
                </v-chip>
                <v-chip v-else color="warning" label>
                  Not verified
                </v-chip>
              </template>
              <template v-slot:item.verify="{ item }">
                <!-- {{item.createdAt}} -->
                <v-btn
                  small
                  v-if="item.status == 1"
                  color="success"
                  class="my-1 mx-2"
                >
                  Transaction Verified
                </v-btn>
                <v-btn
                  v-else
                  small
                  color="success"
                  class="my-1 mx-2"
                  @click="verifyPayment(item.reference)"
                >
                 Verify Transaction
                </v-btn>
              </template>
            </v-data-table>
            <!--------------search campaigns---------------------->
          </v-container>
        </material-card>
      </v-col>
    </v-row>

    <!-- Modals -->
    <!-- Approve Campaign dialogue-->
    <v-dialog
      v-model="approveModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar color="success" dark>
          <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="approveModal = false"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
            {{ modalText.questionText }} <br />
          </p>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="success" @click="approve()">
            {{ modalText.actionText }} <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--Delete Modal-->
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";
let digitadApps = {
  admin: "https://portal.digitadooh.com", //https://digitad.us-east-2.elasticbeanstalk.com
  users: "https://app.digitadooh.com",
  piApp: "https://ourcore.digitadooh.com",
  storage: "https://storage.digitadooh.com",
};
export default {
  name: "DashboardView",

  data: () => ({
    id: "",
    search: "",
    approveModal: false,
    deleteModal: false,
    headers: [
      { text: "ID", value: "id" },

      {
        text: "Reference",
        align: "start",
        value: "reference",
      },
      { text: "Date", value: "date" },
      { text: "Operator", value: "operator" },
      { text: "Amount", value: "amount" },
      { text: "Status", value: "status" },
       { text: "Verification", value: "verify" },
    ],
    currentCampaign: {},
    modalText: {
      headerText: "",
      actionText: "",
      questionText: "",
    },
  }),
  methods: {
    verifyPayment(ref){
      alert(ref);
    },
    campaignDetails(item) {
      let id = item.id;
      this.viewedCampaign.push(item);
      this.$router.push(`/campaign/${id}`);
    },
    setCurrentCampaign(campaign) {
      this.currentCampaign = campaign;
    },
  },
  computed: mapGetters([
    "viewedCampaign",
    "allCampaigns",
    "activeCampaigns",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns",
    "searchMatches",
    "transactions",
  ]),
};
</script>
<style>
.card-elm:hover {
  border: 2px solid orange;
}
.map {
  background-image: url("https://www.nationsonline.org/maps/germany-administrative-map.jpg");
  background-color: #cccccc;
  background-size: 100%;
  width: 100%;
  min-height: 700px;
}
.v-dialog--fullscreen {
  margin-top: 3% !important;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;

  /* margin: 5.2%; */
}
.cursor {
  cursor: pointer;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.task-btn {
  margin: 10px;
}
</style>
